import PropTypes, { InferProps } from 'prop-types'
import { createContext, useEffect, useState } from 'react'

import { ThemeName } from '../services/constants'

const ThemeContext = createContext({
	themeName: '',
	toggleTheme: () => {},
})

const ThemeProviderPropTypes = {
	children: PropTypes.node.isRequired,
}

type ThemeProviderTypes = InferProps<typeof ThemeProviderPropTypes>

const ThemeProvider: React.FC<ThemeProviderTypes> = ({ children }) => {
	const [themeName, setThemeName] = useState(ThemeName.LIGHT)

	useEffect(() => {
		const darkMediaQuery = window.matchMedia('(prefers-color-scheme: dark)')
		setThemeName(darkMediaQuery.matches ? ThemeName.DARK : ThemeName.LIGHT)
		darkMediaQuery.addEventListener('change', (e) => {
			setThemeName(e.matches ? ThemeName.DARK : ThemeName.LIGHT)
		})
	}, [])

	const toggleTheme = () => {
		const name =
			themeName === ThemeName.DARK ? ThemeName.LIGHT : ThemeName.DARK
		localStorage.setItem('themeName', name)
		setThemeName(name)
	}

	return (
		<ThemeContext.Provider value={{ themeName, toggleTheme }}>
			{children}
		</ThemeContext.Provider>
	)
}

export { ThemeProvider, ThemeContext }
